<template>
  <div id="app">
    <Messages
        :user="user"
        :notifications="notifications"
        @removeNotifications="removeNotifications"
    />
    <div>
      <img class="em-messages-modal" style="max-width: 35px; cursor: pointer;" @click="openModal" src="/images/emundus/menus/email.png" />
      <p v-if="notifications > 0" class="notifications__counter">{{counter}}</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Messages from "../../../components/com_emundus_messenger/src/components/Messages";

const qs = require("qs");

export default {
  name: 'Notifications',
  props:{
    user: Number,
    fnum: String
  },
  components: {
    Messages
  },
  data() {
    return {
      counter: 0,
      notifications: {}
    };
  },
  methods: {
    openModal(){
      this.$modal.show('messages');
    },

    getNotifications(){
      axios({
        method: "get",
        url: "index.php?option=com_emundus_messenger&controller=messages&task=getnotifications",
        params: {
          user: this.user,
        },
        paramsSerializer: params => {
           return qs.stringify(params);
        }
      }).then(response => {
        if(response.data.data != null) {
          this.counter = response.data.data.notifications;
          this.notifications = response.data.data;
        }
      });
    },

    removeNotifications(count){
      this.counter -= count;
    }
  },

  created(){
    this.getNotifications();
  }
}
</script>

